import React, {useEffect, useState} from 'react'
import {Button, Col, Container, Figure, Row} from 'react-bootstrap'
import {useHistory} from 'react-router-dom'
import Cookies from 'universal-cookie'
import Swal from 'sweetalert2'
import {TextareaAutosize} from "@material-ui/core";

export default function FoodContent(props) {
    const [title, setTitle] = useState('')
    const [picture, setPicture] = useState('')
    const [price, setPrice] = useState('')
    const [description, setDescription] = useState('')
    const [comment, setComment] = useState('')
    const history = useHistory()
    const URL = process.env.REACT_APP_API_ENDPOINT + '/stores/' + props.store + '/products/' + props.product;

    async function getData() {
        fetch(URL, {
            method: 'GET',
            headers: {
                "Accept": "application/json",
            }
        }).then(response => {
            if (response.ok) {
                return response.json()
            }
            return response.text().then(err => {
                throw new Error(err)
            })
        }).then(response => {
            //console.log(response)
            setPrice(response['price'])
            setDescription(response['description'])
            setTitle(response['name'])
            setPicture(response['image'])
        }).catch(error => {
            //console.log(error.message)
            let response = JSON.parse(error.message)
            Swal.fire({
                title: '!',
                text: `${response.message}\n\n`,
                icon: 'error',
                confirmButtonText: 'Ok'
            }).then(() => history.push('/'))
        })
    }

    const cookies = new Cookies()
    const allcookies = cookies.getAll()

    function Send() {
        const confirmText =
            `
${title}
${price}
${comment}
 OK cancel `
        if (window.confirm(confirmText)) {
            //window.alert('Ok')
            // send transaction
            const url = process.env.REACT_APP_API_ENDPOINT + '/transactions'
            fetch(url, {
                method: 'POST',
                body: JSON.stringify({
                    "name": title,
                    "qty": 1,
                    "store_id": props.store,
                    "product_id": props.product,
                    "comment": comment,
                    "options": {}
                }),
                headers: {
                    "Accept": "application/json",
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${allcookies['session']}`
                }
            }).then(response => {
                if (response.ok) {
                    return response.json()
                }
                return response.text().then(res => {
                    throw new Error(res)
                })
            }).then((res) => {
                // Transaction Made
                Swal.fire({
                    title: '!',
                    html: (
                        `<br>` +
                        `<br>` +
                        `ID <b>${res.id}</b>`),
                    icon: 'success',
                    confirmButtonText: 'Ok'
                }).then(() => history.push('/'))
            }).catch((error) => {
                // Transaction Error
                console.log(error)
                let response = JSON.parse(error.message)
                Swal.fire({
                    title: '!',
                    text: `${response.message}\n\n`,
                    icon: 'error',
                    confirmButtonText: 'Ok'
                }).then(() => history.push('/'))
            })
        } else {
            // fallback to center
            Swal.fire({
                title: '!',
                text: ``,
                icon: 'error',
                confirmButtonText: 'Ok'
            }).then()
        }
    }

    useEffect(() => {
        getData()
    })

    return (
        <React.Fragment>
            <Container>
                <br/>
                <Row>
                    <Col>
                        <Figure>
                            <Figure.Caption className={"text-center"}>
                                <h1>{title}</h1>
                            </Figure.Caption>
                            <Figure.Image
                                width={300}
                                alt=""
                                src={process.env.REACT_APP_API_HOST + '/' + picture}
                                resizeMode="contain"
                            />
                        </Figure>
                    </Col>
                    <Col>{description}</Col>
                </Row>

                <Row>
                    <Col>
                        <h5>{price}</h5>
                    </Col>
                </Row>
                <Row>
                    <Col lg={9}>
                        <TextareaAutosize className="form-control" placeholder="" rows="5"
                                          onChange={event => setComment(event.target.value)}
                                          value={comment}/>
                    </Col>
                    <Col lg={3}>
                        <div style={{display: "flex"}}>
                            <div style={{marginLeft: "auto"}}>
                                <Button variant="primary" onClick={Send}></Button>
                            </div>
                        </div>
                    </Col>
                </Row>

            </Container>
        </React.Fragment>
    )
}
