import React, {useEffect, useState} from 'react'
import Cookies from 'universal-cookie'
import {Card, Container} from 'react-bootstrap'


export default function Transaction(){
    const [transaction,setTransaction] = useState([])

    async function Update(){
        const cookies = new Cookies()
        const allcookies = cookies.getAll()
        const fetchURL = process.env.REACT_APP_API_ENDPOINT + '/transactions'
        fetch(fetchURL,{
            method: 'GET',
            headers:{
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Authorization': `Bearer ${allcookies['session']}`
            }
        }).then(response => {
            if (response.ok) {
                return response.json()
            }
            return response.text().then(res => {
                throw new Error(res)
            })
        }).catch((error) => {
            console.log(error.message)
            let response = JSON.parse(error.message)
            window.alert(
                `${response.message}\n\n`
            )
            window.location.replace('/')
        }).then(r => {
            console.log(r)
            setTransaction(r)
        })
    }

    useEffect(()=>{
        Update()
    },[])

    return(
        <Container>
            <div className={"History"}>
                {
                    transaction && transaction.length>0 && transaction.map((item)=>
                        <Card>
                            <Card.Img variant="top" src={item.picUrl} />
                            <Card.Body style={{display:"flex"}}>
                                <div>
                                    <Card.Title>{item.product_id}</Card.Title>
                                    <Card.Title>{item.total}</Card.Title>
                                    <Card.Text>{item.comment}</Card.Text>
                                    <Card.Text>{item.updated_at}</Card.Text>
                                </div>
                            </Card.Body>
                        </Card>
                    )
                }
            </div>
        </Container>
    )
}