import React from 'react'

import {
    Button,
    Container,
    Form
} from 'react-bootstrap'
import Cookies from "universal-cookie";

class Feedback extends React.Component{
    render() {
        const Send = () => {
            //send form
            // data to send
            let name = document.getElementById('nickName')
            let question = document.getElementById('question')

            if(name.value === ''){
                return
            }
            if(question.value === ''){
                return
            }

            let toSend = new FormData()
            toSend.append('entry.1239005013',name.value)
            toSend.append('entry.664971969',question.value)

            //link to send
            const url = 'https://docs.google.com/forms/u/0/d/e/1FAIpQLSdppxPAZlPuLMgelWDJ1wP6fIB4vScvivNrn3BZ9g7etGklCA/formResponse'

            fetch(url,{
                method: 'POST',
                mode: 'no-cors',
                body: toSend
            }).then(response=>{
                if(response.ok){
                    return response.json()
                }
                return response.text().then(res => {
                    throw new Error(res)
                })
            }).catch(err=>{
                console.log(err.message)
                let response = JSON.parse(err.message)
                window.alert(
                    `${response.message}\n\n`
                )
                return
            }).then(json=>{
                window.alert('message sent')
                console.log(json)
            })
            window.alert('\n')
            const cookies = new Cookies()
            cookies.set('alert','',{path:'/'})
            document.location.replace('#/feedback')
        }
        return (
            <React.Fragment>
                <h1 style={{textAlign: 'center'}}></h1>
                <Container>
                    <Form>
                        <Form.Group>
                            <Form.Label></Form.Label>
                            <Form.Control id='nickName' type="text" placeholder="/" required/>
                            <Form.Text className="text-muted">
                                
                            </Form.Text>
                        </Form.Group>

                        <Form.Group>
                            <Form.Label></Form.Label>
                            <Form.Control as="textarea" rows='5' id='question' required/>
                        </Form.Group>

                        <Button variant="primary" type="submit" onClick={Send}>
                            
                        </Button>
                    </Form>
                </Container>
            </React.Fragment>
        )
    }
}

export default Feedback